






import Vue from "vue";
import { TypeHelper } from "../../../../../../common/shrimp/helpers/typeHelper";
export default Vue.extend({
   name: "VContainer",
   props: {
      eager: {
         default: true
      },
      fluid: {
         default: false
      },
      customCompClass: {
         default: "container",
         type: String
      },
      _renderingProps: {
         type: Object,
      },
   },
   data(){
      return{  
         renderData: true
      }
   },
   destroyed() {
      this.$el?.remove();
   },
   watch:{
      eager(newValue, oldValue){
         if(newValue == true){
            this.renderData=true;
         }
      }
   },
   created() {
      this.renderData = new TypeHelper().cloneObject(this.eager)
   },
});
